.programs {
  padding: 132px 0 0;

  &__title {
    @include vp-767 {
      order: -2;
    }
  }

  &__container {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    row-gap: 40px;
  }

  &__button {
    width: fit-content;

    @include vp-767 {
      order: 1;
    }
  }

  &__swiper {
    flex-grow: 1;
    flex-shrink: 0;
    width: 100%;
  }

  // &__slide {
  //   border-radius: 12px;
  //   overflow: hidden;
  // }

  &__control-wrapper {
    flex-grow: 1;
    flex-shrink: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 52px;
    min-height: 22px;

    @include vp-767 {
      flex-grow: 0;
      flex-shrink: 1;
      width: fit-content;
      order: -1;
    }
  }

  &__scrollbar {
    position: static !important; //Переопределение стилей swiper
    bottom: unset !important; //Переопределение стилей swiper
    left: unset !important; //Переопределение стилей swiper
    width: 100% !important; //Переопределение стилей swiper
    height: 2px !important; //Переопределение стилей swiper
    background-color: rgba($color-cerulean-blue, 0.6) !important; //Переопределение стилей swiper

    & div {
      background-color: $color-cerulean-blue !important; //Переопределение стилей swiper
    }

    @include vp-767 {
      display: none;
    }
  }

  &__nav-buttons-wrapper {
    position: static;
    width: fit-content;
    display: flex;
    gap: 10px;
    min-height: 22px;
  }

  &__nav-button {
    margin: 0 !important; //Переопределение стилей swiper
    position: static !important; //Переопределение стилей swiper
    width: 22px;
    height: 22px;
    display: flex;
    justify-content: center;
    align-items: center;


    &::after {
      display: none;
    }

    &--prev {
      & svg {
        transform: rotate(-135deg);
        stroke-width: 2px;
      }
    }

    &--next {
      & svg {
        transform: rotate(45deg);
        stroke-width: 2px;
      }
    }
  }
}
